<template>
  <component
    :is="props.component || props.tag"
    class="relative inline-block rounded-sm border text-center"
    :class="{
      'px-3 py-1': props.size === 'md',
      'px-4 py-2 text-lg': props.size === 'lg',
      'border-blue-600 text-inherit': props.variant === 'outlined',
      'border-blue-600 bg-blue-700 text-white': props.variant === 'primary',
    }"
    @click.capture="onClick"
  >
    <Transition>
      <div
        v-if="props.loading"
        class="absolute inset-0 flex items-center justify-center"
      >
        <v-spinner
          size="1.3em"
          thickness="0.2em"
          :class="{
            'border-white/30 border-l-white': props.variant === 'primary',
            'border-blue-700/30 border-l-blue-700': props.variant === 'outlined',
          }"
        />
      </div>
    </Transition>
    <div
      class="transition"
      :class="{
        'opacity-0': props.loading,
      }"
    >
      <slot />
    </div>
  </component>
</template>

<script setup lang="ts">
import type { ConcreteComponent } from "vue"

const props = withDefaults(
  defineProps<{
    tag?: string
    component?: string | ConcreteComponent
    variant?: "outlined" | "primary" | "danger"
    size?: "md" | "lg"
    loading?: boolean
  }>(),
  {
    tag: "button",
    variant: "outlined",
    size: "md",
  }
)

function onClick(e: MouseEvent) {
  if (props.tag === "button" && !props.component) {
    e.preventDefault()
  }

  if (props.loading) {
    e.preventDefault()
    e.stopImmediatePropagation()
  }
}
</script>
